export function orderForm() {
    function showActivity() {
        const peopleOnline = document.querySelectorAll('.online-count')

        function randomInteger(min, max) {
            let rand = min + Math.random() * (max + 1 - min);
            return Math.floor(rand);
        }

        setInterval(function () {
            let randNum = randomInteger(120, 260);
            for (let i = 0; i < peopleOnline.length; i++) peopleOnline[i].textContent = randNum
        }, 15000);
    }

    window.addEventListener('load', showActivity)
}